.o-scale-unitcontainer {
  display: flex;
  justify-content: space-around;
  width: 400px;
  max-width: 100%;
  border-radius: 2px;
  border: 1px solid #8797C8;
}

.m-scale-unit {
  position: relative;
  cursor: pointer;
  flex: 1 0 0;
  text-align: center;
  height: 52px;
  align-content: center;
}

.m-scale-unit::before {
  position: absolute;
  left: 4px;
  top: 4px;
  display: block;
  content: " ";
  height: calc(100% - 8px);
  width: calc(100% - 8px);
  background-color: #B4BDDB;
  border-radius: 4px;
}

.m-scale-unit.current-value::before,
.o-scale-unitcontainer:hover > .m-scale-unit::before {
  background-color: #317474;
}

.m-scale-unit.current-value,
.o-scale-unitcontainer:hover > .m-scale-unit {
  color: #FAFAFA;
}

.o-scale-unitcontainer > .m-scale-unit.current-value ~ .m-scale-unit,
.o-scale-unitcontainer > .m-scale-unit:hover ~ .m-scale-unit {
  color: #324371;
}

.o-scale-unitcontainer > .m-scale-unit.current-value ~ .m-scale-unit::before,
.o-scale-unitcontainer > .m-scale-unit:hover ~ .m-scale-unit::before {
  background-color: #B4BDDB;
}

.a-image-multistate {
  display: block;
  margin: 0 auto;
  transition: transform 0.3s ease;
}

.a-label-datavalue {
  visibility: hidden;
}

.a-label-datavalue.showValue {
  visibility: visible;
  position: absolute;
  top: 32px;
  left: 30px;
  color: #57699C;
}

.a-label-datavalue.showValue.highlighted {
  color: #FAFAFA;
}